:root {
  --very-dark-blue-bg-primary: hsl(233, 47%, 7%);
  --dark-desaturated-blue-card-bg: hsl(244, 38%, 16%);
  --soft-violet-accent: hsl(277, 64%, 61%);
  --white-main-heading-stats: hsl(0, 0%, 100%);
  --slightly-transparent-white-main-p: hsla(0, 0%, 100%, 0.75);
  --slightly-transparent-white-stat-headings: hsla(0, 0%, 100%, 0.6);
}

:root {
  --inter: "Inter", sans-serif;
  --lexend-deca: "Lexend Deca", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--inter);
  background: var(--very-dark-blue-bg-primary);
}

figure {
  margin: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

mark {
  background: none;
}

button {
  border: none;
  background: none;
}

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin: 4.4375rem auto;
}

.card {
  background: var(--dark-desaturated-blue-card-bg);
  border-radius: 0.625rem;
  overflow: hidden;
  width: 20.4375rem;
  display: flex;
  flex-direction: column;
  font-size: 0.9375rem;
}
.card__header {
  width: 100%;
  height: 15rem;
  background: #aa5cdb no-repeat top/100% url("../../../images/image-header-mobile.jpg");
  overflow: hidden;
}
@media (min-width: 40em) {
  .card__header {
    background: url("../../../images/image-header-desktop.jpg");
  }
}
.card__header::after {
  content: "";
  position: absolute;
  border-radius: 0.625rem 0.625rem 0 0;
  width: 20.4375rem;
  height: 15rem;
  background: rgba(115, 4, 185, 0.61);
}
.card__title, .card__description {
  padding: 0 1.75rem;
}
.card__title {
  margin-top: 2.125rem;
  text-align: center;
  font-size: 1.6875rem;
  font-weight: 700;
  color: var(--white-main-heading-stats);
}
.card__title mark {
  color: var(--soft-violet-accent);
}
.card__description {
  margin-top: 1.0625rem;
  text-align: center;
  line-height: 1.4375rem;
  font-weight: 400;
  color: var(--slightly-transparent-white-main-p);
}
.card__stats {
  margin-top: 0.6875rem;
  margin-bottom: 1.9375rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.card__stats__title {
  margin-top: 1.75rem;
  font-size: 1.6875rem;
  font-weight: 700;
  color: var(--white-main-heading-stats);
}
.card__stats__description {
  margin-top: 0.625rem;
  text-transform: uppercase;
  color: var(--slightly-transparent-white-stat-headings);
}